import 'babel-polyfill'
import '@babel/polyfill'
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/store'
import './registerServiceWorker'
import firebase from "firebase";
import VueChatScroll from 'vue-chat-scroll'

Vue.use(VueChatScroll)
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)
import * as VueGoogleMaps from "vue2-google-maps";
import './assets/css/main.css';
import i18n from "./i18n";
import vuetify from '@/plugins/vuetify';

Vue.use(VueGoogleMaps, {
    load: {
        key: "AIzaSyBYhPYAZkG6ML2kRArdK8xITPrZWzF0JSw",
        libraries: "places" // necessary for places input
    }
});

Vue.config.productionTip = false

let app = '';

let config = {
    apiKey: "AIzaSyABGil2afckXxQ127FS8fW6y5phy1wzd6g",
    authDomain: "tfg-vue-m3m.firebaseapp.com",
    databaseURL: "https://tfg-vue-m3m.firebaseio.com",
    projectId: "tfg-vue-m3m",
    storageBucket: "tfg-vue-m3m.appspot.com",
    messagingSenderId: "144506012173",
    appId: "1:144506012173:web:8a58aa4b50d8b336184b44"
};
firebase.initializeApp(config);

// Initialize Cloud Firestore through Firebase
let db = firebase.firestore();

const storage = firebase.storage()

let messaging = null;
try {
    messaging = firebase.messaging()
} catch (e) {
    console.log("This browser does not support firebase messaging.")
}

export default {
    db,
    storage,
    messaging,
    i18n,
};


firebase.auth().onAuthStateChanged(() => {
    if (!app) {
        app = new Vue({
            router,
            store,
            i18n,
            render: h => h(App),
            vuetify,
            components: {App}
        }).$mount('#app');
    }
})

import VuetifyGoogleAutocomplete from 'vuetify-google-autocomplete';

Vue.use(VuetifyGoogleAutocomplete, {
    apiKey: 'AIzaSyBYhPYAZkG6ML2kRArdK8xITPrZWzF0JSw',
    language: i18n.locale,
});
