import Vue from 'vue';
import VueI18n from 'vue-i18n';
import I18NMessages from "./I18NMessages";

Vue.use(VueI18n);

let localeLanguage = 'es';

if (navigator.language === 'es' || navigator.language === 'es-ES') {
    localeLanguage = 'es';
} else if (navigator.language === 'en' || navigator.language === 'en-US') {
    localeLanguage = 'en';
}

const i18n = new VueI18n({
    locale: localeLanguage, // set locale
    messages: I18NMessages.messages, // set locale messages
    dateTimeFormats: I18NMessages.dateTimeFormats
});

export default i18n;