import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import database from "./database.js";
import users from "./users.js";
import i18n from "../../i18n";

let alreadyAccountUsernameError = i18n.t('messages.ERROR_ALREADY_AN_ACCOUNT_WITH_THIS_USERNAME');
let genericError = i18n.t('messages.ERROR_GENERIC_PROBLEM');
let gettingCurrentLocationError = i18n.t('messages.ERROR_GETTING_CURRENT_LOCATION');

////////////////////////////////////////////////////////
//-----         ACCOUNTS & PROFILES INFO         -----//
////////////////////////////////////////////////////////

// @vuese
// Mdulo del estado centralizado (Store) encargado de la gestin del usuario autenticado
export default {
    namespaced: true,
    modules: {
        database,
        users
    },
    state: {
        /* User account info */
        // @vuese
        // ID del usuario autenticado
        id: null,
        // @vuese
        // Nombre del usuario autenticado
        'name': null,
        // @vuese
        // Apellidos del usuario autenticado
        lastName: null,
        // @vuese
        // E-mail del usuario autenticado
        email: null,
        // @vuese
        // Lista de identificadores de los chats del usuario autenticado
        chats: [],
        // @vuese
        // Rol del usuario autenticado en la aplicacin
        role: 'user',
        // @vuese
        // Language seleccionado por el usuario autenticado para la aplicacin
        language: null,

        /* User profile */
        // 1st part
        // @vuese
        // Nombre de usuario del usuario autenticado
        username: null,
        // @vuese
        // Fecha de nacimiento del usuario autenticado
        birthDate: null,
        // @vuese
        // Gnero del usuario autenticado
        gender: null,
        // @vuese
        // Descripcin del usuario autenticado
        description: null,
        // @vuese
        // Valoracin media del usuario autenticado
        avgRating: null,
        // @vuese
        // Nmero de valoraciones realizadas al usuario autenticado
        numRatings: null,
        // 2nd part
        // @vuese
        // Idiomas conocidos del usuario autenticado
        knownLanguages: null,
        // @vuese
        // Idiomas a practicar del usuario autenticado
        languagesToLearn: null,
        // 3rd part
        // @vuese
        // Imagen de perfil del usuario autenticado
        profileImg: null,

        /* Geolocation info */
        // @vuese
        // Localizacin del usuario autenticado
        location: null,

        /* Ratings info */
        // @vuese
        // Valoraciones realizadas por el usuario autenticado
        ratings: null,

        /* Constants */
        // @vuese
        // Gneros disponibles en al aplicacin
        genderOptions: {
            "M": {
                img: "img/genders/male.svg",
                defaultProfileImg: 'img/defaultUserImages/defaultUserImage.svg'
            },
            "W": {
                img: "img/genders/female.svg",
                defaultProfileImg: 'img/defaultUserImages/defaultUserImage.svg'
            },
            "O": {
                img: "img/genders/other.svg",
                defaultProfileImg: 'img/defaultUserImages/defaultUserImage.svg'
            }
        },

        // @vuese
        // Idiomas disponibles en al aplicacin
        languageOptions: {
            "de": {
                img: "img/flags/de.svg"
            },
            "zh": {
                img: "img/flags/zh.svg"
            },
            "es": {
                img: "img/flags/es.svg"
            },
            "fr": {
                img: "img/flags/fr.svg"
            },
            "en": {
                img: "img/flags/en.svg"
            },
            "it": {
                img: "img/flags/it.svg"
            },
            "ja": {
                img: "img/flags/ja.svg"
            },
            "pt": {
                img: "img/flags/pt.svg"
            },
            "all": {
                img: "img/flags/all.svg"
            }
        }
    },
    getters: {
        // @vuese
        // Devuelve el nombre del usuario autenticado
        // @arg El state del user store
        getName: (state) => {
            return state.name;
        },

        // @vuese
        // Devuelve el ID del usuario autenticado
        // @arg El state del user store
        getID: (state) => {
            return state.id;
        },

        // @vuese
        // Devuelve los apellidos del usuario autenticado
        // @arg El state del user store
        getLastName: (state) => {
            return state.lastName;
        },

        // @vuese
        // Devuelve el E-mail del usuario autenticado
        // @arg El state del user store
        getEmail: (state) => {
            return state.email;
        },

        // @vuese
        // Devuelve el rol del usuario autenticado
        // @arg El state del user store
        getRole: (state) => {
            return state.role;
        },

        // @vuese
        // Devuelve los identificadores de los chats del usuario autenticado
        // @arg El state del user store
        getChats: (state) => {
            return state.chats;
        },

        // @vuese
        // Devuelve la informacin de perfil del usuario autenticado
        // @arg El state del user store
        getProfile: (state) => {
            let data = {};

            if (state.username) {
                data = {
                    'username': state.username,
                    ...data
                }
            }
            if (state.birthDate) {
                data = {
                    'birthDate': state.birthDate,
                    ...data
                }
            }
            if (state.gender) {
                data = {
                    'gender': state.gender,
                    ...data
                }
            }
            if (state.description) {
                data = {
                    'description': state.description,
                    ...data
                }
            }
            if (state.profileImg) {
                data = {
                    'profileImg': state.profileImg,
                    ...data
                }
            }
            if (state.knownLanguages) {
                data = {
                    'knownLanguages': state.knownLanguages,
                    ...data
                }
            }
            if (state.languagesToLearn) {
                data = {
                    'languagesToLearn': state.languagesToLearn,
                    ...data
                }
            }

            data = {
                'avgRating': state.avgRating,
                'numRatings': state.numRatings,
                ...data
            }

            return data;
        },

        // @vuese
        // Devuelve la fecha de nacimiento del usuario autenticado
        // @arg El state del user store
        getBirthDate: (state) => {
            return state.birthDate;
        },

        // @vuese
        // Devuelve el gnero del usuario autenticado
        // @arg El state del user store
        getGender: (state) => {
            return state.gender;
        },

        // @vuese
        // Devuelve la descripcin del usuario autenticado
        // @arg El state del user store
        getDescription: (state) => {
            return state.description;
        },

        // @vuese
        // Devuelve la imagen de perfil del usuario autenticado
        // @arg El state del user store
        getProfileImg: (state) => {
            return state.profileImg;
        },

        // @vuese
        // Devuelve los idiomas conocidos por el usuario autenticado
        // @arg El state del user store
        getKnownLanguages: (state) => {
            return state.knownLanguages;
        },

        // @vuese
        // Devuelve los idiomas a practicar por el usuario autenticado
        // @arg El state del user store
        getLanguagesToLearn: (state) => {
            return state.languagesToLearn;
        },

        // @vuese
        // Obtiene la imagen asociada a un gnero
        // @arg El state del user store
        // @arg ID del gnero
        getGenderImgByID: (state) => (id) => {
            return state.genderOptions[id].img;
        },

        // @vuese
        // Devuelve la imagen de perfil por defecto
        // @arg El state del user store
        getGenderDefaultProfileImgByUserGender: (state) => {
            return state.genderOptions["O"].defaultProfileImg;
        },

        // @vuese
        // Devuelve los gneros disponibles en la aplicacin en forma de Array
        // @arg El state del user store
        // @arg Los getters del user store
        getGenderOptionsAsArray: (state, getters) => {
            let genderOptions = {
                "M": {
                    'text': i18n.t('messages.TEXT_MAN')
                },
                "W": {
                    'text': i18n.t('messages.TEXT_WOMAN')
                },
                "O": {
                    'text': i18n.t('messages.TEXT_OTHER')
                }
            };
            return Object.keys(genderOptions).map(function (id) {
                let text = genderOptions[id].text;
                let img = getters.getGenderImgByID(id);
                return new Object({id, text, img});
            });
        },

        // @vuese
        // Devuelve la imagen correspondiente un idioma
        // @arg El state del user store
        // @arg ID del idioma
        getLanguageImgByID: (state) => (id) => {
            return state.languageOptions[id].img;
        },

        // @vuese
        // Devuelve los idiomas disponibles en la aplicacin en forma de Array
        // @arg El state del user store
        // @arg Los getters del user store
        getLanguageOptionsAsArray: (state, getters) => {
            let languageOptions = {
                "de": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_DE')
                },
                "zh": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ZH')
                },
                "es": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ES')
                },
                "fr": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_FR')
                },
                "en": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_EN')
                },
                "it": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_IT')
                },
                "ja": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_JA')
                },
                "pt": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_PT')
                },
                "all": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ALL')
                }
            };
            return Object.keys(state.languageOptions).map(function (id) {
                let text = languageOptions[id].text;
                let img = getters.getLanguageImgByID(id);
                return new Object({id, text, img});
            });
        },

        // @vuese
        // Devuelve la informacin de un idioma
        // @arg El state del user store
        // @arg El ID del idioma
        getLanguageFromLanguageOptionsByID: (state) => (id) => {
            let languageOptions = {
                "de": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_DE')
                },
                "zh": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ZH')
                },
                "es": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ES')
                },
                "fr": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_FR')
                },
                "en": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_EN')
                },
                "it": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_IT')
                },
                "ja": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_JA')
                },
                "pt": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_PT')
                },
                "all": {
                    'text': i18n.t('messages.TEXT_LANGUAGE_ALL')
                }
            };

            return {
                ...state.languageOptions[id],
                'text': languageOptions[id].text
            };
        },

        // @vuese
        // Devuelve la localizacin del usuario
        // @arg El state del user store
        getLocation: (state) => {
            return state.location;
        },

        // @vuese
        // Devuelve las valoraciones realizadas por el usuario
        // @arg El state del user store
        getRatings: (state) => {
            return state.ratings;
        },

        // @vuese
        // Devuelve la informacin de una valoracin realizada por el usuario
        // @arg El state del user store
        // @arg ID de la valoracin
        getRating: (state) => (id) => {
            if (state.ratings) {
                return state.ratings[id];
            } else {
                return null;
            }
        }
    },
    mutations: {
        // @vuese
        // Actualiza el valor del nombre del usuario autenticado
        // @arg El state del user store
        // @arg Nombre
        setName(state, name) {
            state.name = name;
        },

        // @vuese
        // Actualiza el idioma seleccionado para la aplicacin por el usuario autenticado
        // @arg El state del user store
        // @arg Nombre
        setLanguage(state, language) {
            i18n.locale = language;
            state.language = language;
        },

        // @vuese
        // Actualiza el valor de los apellidos del usuario autenticado
        // @arg El state del user store
        // @arg Apellidos
        setLastName(state, lastName) {
            state.lastName = lastName;
        },

        // @vuese
        // Actualiza el valor del E-mail del usuario autenticado
        // @arg El state del user store
        // @arg E-mail
        setEmail(state, email) {
            state.email = email;
        },

        // @vuese
        // Actualiza los valores de los campos de la cuenta del usuario autenticado
        // @arg El state del user store
        // @arg accountInfo{[email], [name], [lastName], [profileImg], [id], [chats], [role], [language]}
        setAccount(state, accountInfo) {
            if (accountInfo.hasOwnProperty('email')) {
                state.email = accountInfo.email;
            }
            if (accountInfo.hasOwnProperty('name')) {
                state.name = accountInfo.name;
            }
            if (accountInfo.hasOwnProperty('lastName')) {
                state.lastName = accountInfo.lastName;
            }
            if (accountInfo.hasOwnProperty('profileImg')) {
                state.role = accountInfo.role;
            }
            if (accountInfo.hasOwnProperty('id')) {
                state.id = accountInfo.id;
            }
            if (accountInfo.hasOwnProperty('chats')) {
                state.chats = accountInfo.chats;
            }
            if (accountInfo.hasOwnProperty('role')) {
                state.role = accountInfo.role;
            }
            if (accountInfo.hasOwnProperty('language') && accountInfo.language != null) {
                i18n.locale = accountInfo.language;
                state.language = accountInfo.language;
            }
        },

        // @vuese
        // Actualiza el valor del nombre de usuario del usuario autenticado
        // @arg El state del user store
        // @arg Nombre de usuario
        setUsername(state, username) {
            state.username = username;
        },

        // @vuese
        // Actualiza el valor de la fecha de nacimiento del usuario autenticado
        // @arg El state del user store
        // @arg Fecha de nacimiento
        setBirthDate(state, birthDate) {
            state.birthDate = birthDate;
        },

        // @vuese
        // Actualiza el valor del gnero del usuario autenticado
        // @arg El state del user store
        // @arg Gnero
        setGender(state, gender) {
            state.gender = gender;
        },

        // @vuese
        // Actualiza el valor del descripcin del usuario autenticado
        // @arg El state del user store
        // @arg Descripcin
        setDescription(state, description) {
            state.description = description;
        },

        // @vuese
        // Actualiza el valor de la valoracin media del usuario autenticado
        // @arg El state del user store
        // @arg Valoracin media
        setAvgRating(state, avgRating) {
            state.avgRating = avgRating;
        },

        // @vuese
        // Actualiza el valor del nmero de valoraciones al usuario autenticado
        // @arg El state del user store
        // @arg Nmero de valoracione
        setNumRatings(state, numRatings) {
            state.numRatings = numRatings;
        },

        // @vuese
        // Actualiza el valor de los idiomas conocidos por el usuario autenticado
        // @arg El state del user store
        // @arg Idiomas conocidos
        setKnownLanguages(state, knownLanguages) {
            state.knownLanguages = knownLanguages;
        },

        // @vuese
        // Actualiza el valor del idiomas a practicar por el usuario autenticado
        // @arg El state del user store
        // @arg Idiomas a practicar
        setLanguagesToLearn(state, languagesToLearn) {
            state.languagesToLearn = languagesToLearn;
        },

        // @vuese
        // Actualiza el valor de la imagen de perfil del usuario autenticado
        // @arg El state del user store
        // @arg Imagen de perfil
        setProfileImg(state, profileImg) {
            state.profileImg = profileImg;
        },

        // @vuese
        // Actualiza el valor de las valoraciones del usuario autenticado
        // @arg El state del user store
        // @arg Valoraciones
        setRatings(state, ratings) {
            state.ratings = ratings;
        },

        // @vuese
        // Actualiza el valor de una valoracin del usuario autenticado
        // @arg El state del user store
        // @arg data{id, rating}
        setRating(state, data) {
            if (state.ratings == null) {
                state.ratings = {};
            }
            state.ratings[data.id] = data.rating;
        },

        // @vuese
        // Actualiza los valores de los campos del perfil del usuario autenticado
        // @arg El state del user store
        // @arg accountInfo{[username], [birthDate], [gender], [description], [avgRating], [numRatings], [knownLanguages], [languagesToLearn], [profileImg]}
        setProfile(state, profileInfo) {
            if (profileInfo) {

                if (profileInfo.hasOwnProperty('username')) {
                    state.username = profileInfo.username;
                }
                if (profileInfo.hasOwnProperty('birthDate')) {
                    state.birthDate = profileInfo.birthDate;
                }
                if (profileInfo.hasOwnProperty('gender')) {
                    state.gender = profileInfo.gender;
                }
                if (profileInfo.hasOwnProperty('description')) {
                    state.description = profileInfo.description;
                }
                if (profileInfo.hasOwnProperty('avgRating')) {
                    state.avgRating = profileInfo.avgRating;
                }
                if (profileInfo.hasOwnProperty('numRatings')) {
                    state.numRatings = profileInfo.numRatings;
                }
                if (profileInfo.hasOwnProperty('knownLanguages')) {
                    state.knownLanguages = profileInfo.knownLanguages;
                }
                if (profileInfo.hasOwnProperty('languagesToLearn')) {
                    state.languagesToLearn = profileInfo.languagesToLearn;
                }
                if (profileInfo.hasOwnProperty('profileImg')) {
                    state.profileImg = profileInfo.profileImg;
                }
            }
        },

        // @vuese
        // Restablece todas las variables del state a sus valores por defecto
        // @arg El state del user store
        resetState(state) {
            /* User account*/
            state.id = null;
            state.name = null;
            state.lastName = null;
            state.email = null;
            state.role = 'user';
            state.chats = [];

            /* User profile */
            // 1st part
            state.username = null;
            state.birthDate = null;
            state.gender = null;
            state.description = null;
            state.avgRating = null;
            state.numRatings = null;
            // 2nd part
            state.knownLanguages = null;
            state.languagesToLearn = null;
            // 3rd part
            state.profileImg = null;

            /* Ratings info */
            state.ratings = null;

            /* Geolocation info */
            state.location = null;
        },

        // @vuese
        // Actualiza el valor de la localizacin del usuario autenticado
        // @arg El state del user store
        // @arg Localizacin
        setLocation(state, location) {
            state.location = location;
        }
    },
    actions: {
        // @vuese
        // Inicia el state con los datos apropiados
        // @arg El context del user store
        initState(context) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/getProfile')
                    .then((profileInfo) => {
                        context.commit('setProfile', profileInfo);

                        context.dispatch('database/getAccount')
                            .then((accountInfo) => {
                                context.commit('setAccount', accountInfo);
                                context.dispatch('getRatings')
                                    .then(() => {
                                        resolve();
                                    })
                                    .catch((error) => {
                                        reject(error)
                                    });
                                resolve();
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        // @vuese
        // Resetea el estado del user store
        // @arg El context del user store
        resetState(context) {
            return new Promise(function (resolve) {
                context.commit('resetState');
                resolve();
            });
        },

        // @vuese
        // Crea una cuenta de usuario con los datos pasados como parmetro
        // @arg El context del user store
        // @arg data {email, password, name, lastName}
        createAccount(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/createAccount', data)
                    .then(() => {
                        context.dispatch('database/setPersistance')
                            .then(() => {
                                context.dispatch('database/setAccount', data)
                                    .then(() => {
                                        context.commit('setAccount', data);
                                        resolve();
                                    })
                                    .catch((error) => {
                                        console.log('Error creating user account.');
                                        reject(error)
                                    });
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Actualiza y guarda el idioma de la aplicacin
        // @arg El context del user store
        // @arg Identificador del idioma
        //----------------------------------------------------------------------------->
        // ~ setLanguage ~                                                             >
        // To set app language for that user                                           >
        // parameters required: lang                                                   >
        //----------------------------------------------------------------------------->
        setLanguage(context, lang) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/setAccountLanguage', lang)
                    .then(() => {
                        context.commit('setLanguage', lang);
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Genera un token de notificacin y lo guarda
        // @arg El context del user store
        generateNotificationToken(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/generatePushNotificationToken')
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Actualiza y guarda el nombre y apellidos del usuario con los datos pasados como parmetro
        // @arg El context del user store
        // @arg data {[name], [lastName]}
        updateAccountNameAndLastName(context, data) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/updateAccountData', data)
                    .then(() => {
                        if (data.hasOwnProperty('name')) {
                            context.commit('setName', data.name);
                        }
                        if (data.hasOwnProperty('lastName')) {
                            context.commit('setLastName', data.lastName);
                        }

                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Actualiza y guarda el E-mail del usuario pasado como parmetro
        // @arg El context del user store
        // @arg E-mail
        updateAccountEmail(context, email) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/updateAccountEmail', email)
                    .then(() => {
                        context.commit('setEmail', email);
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Actualiza y guarda la contrasea con la pasada como parmetro
        // @arg El context del user store
        // @arg Contrasea
        updateAccountPassword(context, password) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/updateAccountPassword', password)
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Realiza el login del usuario con los datos pasados como parmetro
        // @arg El context del user store
        // @arg data {email, password}
        login(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/login', data)
                    .then(() => {
                        context.commit('setAccount', data);
                        context.dispatch('database/setPersistance')
                            .then(() => {
                                resolve();
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Comprueba si el usuario est loggeado
        // @arg El context del user store
        isUserLogged: function (context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/isUserAuth')
                    .then((isIt) => {
                        resolve(isIt);
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Cierra la sesin del usuario
        // @arg El context del user store
        logout(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/logout')
                    .then(() => {
                        /*context.dispatch('resetAppState', null, {root: true})
                            .then(function () {
                                resolve();
                            }).catch((error) => {
                                reject(error)
                        });*/
                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Enva un correo electrnico de verificacin al E-mail del usuario
        // @arg El context del user store
        sendEmailVerification(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/sendEmailVerification')
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error sending verification email.');
                        reject(error);
                    });
            });
        },

        // @vuese
        // Enva un correo electrnico de reseteo de contrasea al E-mail pasado como parmetro
        // @arg El context del user store
        // @arg E-mail
        sendPasswordResetEmail(context, email) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/sendPasswordResetEmail', email)
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error sending password email.');
                        reject(error);
                    });
            });
        },

        // @vuese
        // Comprueba si el E-mail del usuario est verificado
        // @arg El context del user store
        checkIfEmailIsVerified(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/isEmailVerified')
                    .then((isVerified) => {
                        resolve(isVerified);
                    })
                    .catch((error) => {
                        console.log('Error checking if email is verified.');
                        reject(error);
                    });
            });
        },

        // @vuese
        // Comprueba si un nombre de usuario est registrado en la aplicacin
        // @arg El context del user store
        // @arg Nombre de usuario
        checkIfUsernameIsAlreadyRegistered(context, username) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/getProfileByUsername', username)
                    .then((profileInfo) => {
                        if (profileInfo) {
                            resolve(true);
                        } else {
                            resolve(false);
                        }
                    })
                    .catch((error) => {
                        console.log('Error checking if username is already registered.');
                        reject(error)
                    });
            });
        },

        // @vuese
        // Crea y almacena el perfil del usuario con los datos pasados como parmetro
        // @arg El context del user store
        // @arg data {username, birthDate, gender, description}
        createProfile(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('checkIfUsernameIsAlreadyRegistered', data.username)
                    .then((isAlreadyRegistered) => {
                        if (isAlreadyRegistered) {
                            reject(alreadyAccountUsernameError);
                        } else {
                            context.dispatch('database/setProfile', data)
                                .then(() => {
                                    context.commit('setUsername', data.username);
                                    context.commit('setBirthDate', data.birthDate);
                                    context.commit('setGender', data.gender);
                                    context.commit('setDescription', data.description);
                                    context.commit('setAvgRating', 0);
                                    context.commit('setNumRatings', 0);
                                    resolve();
                                })
                                .catch((error) => {
                                    console.log('Error creating user profile.');
                                    reject(error);
                                });
                        }
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Actualiza y guarda el perfil del usuario con los datos pasados como parmetro
        // @arg El context del user store
        // @arg data {[username], [birthDate], [gender], [description], [knownLanguages], [languagesToLearn]}
        updateProfile(context, data) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/updateProfile', data)
                    .then(async () => {

                        if (data.hasOwnProperty('username')) {
                            context.commit('setUsername', data.username);
                        }
                        if (data.hasOwnProperty('description')) {
                            context.commit('setDescription', data.description);
                        }
                        if (data.hasOwnProperty('gender')) {
                            context.commit('setGender', data.gender);
                        }
                        if (data.hasOwnProperty('birthDate')) {
                            context.commit('setBirthDate', data.birthDate);
                        }
                        if (data.hasOwnProperty('knownLanguages')) {
                            context.commit('setKnownLanguages', data.knownLanguages);
                        }
                        if (data.hasOwnProperty('languagesToLearn')) {
                            context.commit('setLanguagesToLearn', data.languagesToLearn);
                        }

                        resolve();
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });
        },

        // @vuese
        // Actualiza y guarda los idiomas conocidos y a practicar del usuario
        // @arg El context del user store
        // @arg data {knownLanguages, languagesToLearn}
        setLanguages(context, data) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/setLanguages', data)
                    .then(() => {
                        context.commit('setKnownLanguages', data.knownLanguages);
                        context.commit('setLanguagesToLearn', data.languagesToLearn);
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error setting user languages.');
                        reject(error);
                    });
            });
        },

        // @vuese
        // Actualiza y guarda la imagen de perfil con la pasada como parmetro
        // @arg El context del user store
        // @arg img
        setProfileImg(context, img) {
            return new Promise(function (resolve, reject) {
                Vue.nextTick(() => {

                    context.dispatch('database/uploadProfileImg', img)
                        .then((url) => {
                            context.dispatch('database/setProfileImg', url)
                                .then(() => {
                                    context.commit('setProfileImg', url);
                                    resolve();
                                })
                                .catch((error) => {
                                    console.log('Error setting user profile image.');
                                    console.error(error)
                                    reject(error);
                                });
                        })
                        .catch((error) => {
                            console.log('Error uploading user profile image.');
                            reject(error);
                        });
                });
            });
        },

        // @vuese
        // Actualiza y guarda el estado de registro del usuario como completado
        // @arg El context del user store
        setRegistrationCompleted(context) {
            return new Promise(function (resolve, reject) {

                context.dispatch('database/setRegistrationCompleted')
                    .then(() => {
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error updating user registration state.');
                        reject(error);
                    });
            });
        },

        // @vuese
        // Obtiene el estado de registro del usuario
        // @arg El context del user store
        getRegistrationState(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/getProfile')
                    .then((profileInfo) => {

                        context.dispatch('checkRegistrationState', profileInfo)
                            .then((state) => {
                                context.commit('setProfile', profileInfo);
                                resolve(state);
                            })
                            .catch((error) => {
                                console.log(error);
                                reject(error);
                            });
                    })
                    .catch((error) => {
                        console.error(error);
                        reject(genericError);
                    });
            });
        },

        // @vuese
        // Evala el estado de registro del usuario
        // @arg El context del user store
        // @arg data {username, gender, description, avgRating, numRatings, knownLanguages, languagesToLearn, profileImg}
        checkRegistrationState(context, data) {

            return new Promise(function (resolve, reject) {

                context.dispatch('checkIfEmailIsVerified')
                    .then((isVerified) => {
                        if (isVerified) {
                            // Part 0 completed
                            if (data && data.hasOwnProperty('username') && data.hasOwnProperty('gender')
                                && data.hasOwnProperty('description') && data.hasOwnProperty('avgRating')
                                && data.hasOwnProperty('numRatings')) {
                                // Part 1 completed
                                if (data.hasOwnProperty('knownLanguages') && data.hasOwnProperty('languagesToLearn')) {
                                    // Part 2 completed
                                    if (data.hasOwnProperty('profileImg')) {
                                        // Part 3 completed
                                        // Registration process completed
                                        resolve('dashboard');
                                    } else {
                                        // State 3
                                        resolve('set-your-profile-image');
                                    }
                                } else {
                                    // State 2
                                    resolve('fill-your-languages');
                                }
                            } else {
                                // State 1
                                resolve('fill-your-profile-info');
                            }
                        } else {
                            // State 0
                            resolve('email-verification');
                        }
                    })
                    .catch((error) => {
                        console.error(error);
                        reject(genericError);
                    });
            });
        },

        // @vuese
        // Obtiene y almacena la localizacin actual del usuario
        getCurrentLocation() {
            return new Promise(function (resolve, reject) {
                try {
                    navigator.geolocation.getCurrentPosition(position => {
                            resolve({
                                lat: position.coords.latitude,
                                lng: position.coords.longitude
                            })
                        },
                        function (error) {
                            console.error(error);
                            reject(gettingCurrentLocationError);
                        });
                } catch (e) {
                    console.log(e)
                }

            });
        },

        // @vuese
        // Obtiene las valoraciones realizadas por el usuario y las almacena
        // @arg El context del user store
        getRatings(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/getRatings')
                    .then((ratingsInfo) => {
                        context.commit('setRatings', ratingsInfo);
                        resolve();
                    })
                    .catch((error) => {
                        console.log('Error retrieving user ratings.');
                        reject(error);
                    });
            });
        },

        // @vuese
        // Actualiza y guarda una valoracin realizada por el usuario con los datos pasados como parmetro
        // @arg El context del user store
        // @arg data {id, rating}
        rateUser(context, data) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/setRating', data)
                    .then(() => {
                        context.commit('setRating', data);
                        context.commit('users/addRating', data);

                        resolve();
                    })
                    .catch((error) => {
                        console.log(error)
                        console.log('Error rating user.');
                        reject(error);
                    });
            });
        },

        // @vuese
        // Obtiene y almacena la localizacin del usuario
        // @arg El context del user store
        getLocation(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('getCurrentLocation')
                    .then((currentLocation) => {
                        context.commit('setLocation', currentLocation);
                        context.dispatch('database/updateUserLocation', currentLocation)
                            .then(() => {
                                resolve(currentLocation);
                            })
                            .catch(() => {
                                resolve(currentLocation);
                            });
                    })
                    .catch((error) => {
                        console.error(error);
                        context.dispatch('database/getLocation')
                            .then((location) => {
                                context.commit('setLocation', location);
                                resolve(location);
                            })
                            .catch((error) => {
                                reject(error);
                            });
                    });
            });
        }
    }
};