import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import AuxMethods from "../AuxMethods";
import database from "./database";

////////////////////////////////////////////////////////
//-----        APP PERMISSIONS MANAGEMENT        -----//
////////////////////////////////////////////////////////

// @vuese
// Mdulo del estado centralizado (Store) encargado de la gestin de los permisos en el navegador para la aplicacin
export default {
    namespaced: true,
    modules: {
        database
    },
    state: {
        // @vuese
        // Almacena el estado de concesin del permiso de geolocalizacin
        isGeolocationPermissionGranted: false,

        // @vuese
        // Almacena el estado de concesin del permiso de notificaciones
        isPushNotificationPermissionGranted: false
    },
    getters: {
        // @vuese
        // Utilizado para obtener el estado de concesin del permiso de geolocalizacin
        // @arg El state del permissions store
        getIsGeolocationPermissionGranted: (state) => {
            return state.isGeolocationPermissionGranted;
        },

        // @vuese
        // Utilizado para obtener el estado de concesin del permiso de notificaciones
        // @arg El state del permissions store
        getIsPushNotificationPermissionGranted: (state) => {
            return state.isPushNotificationPermissionGranted;
        }
    },
    mutations: {
        // @vuese
        // Restablece todas las variables del state a sus valores por defecto
        // @arg El state del permissions store
        resetState(state) {
            state.isGeolocationPermissionGranted = false;
            state.isPushNotificationPermissionGranted = false;
        },

        // @vuese
        // Actualiza el valor del estado de concesin del permiso de geolocalizacin con el valor pasado como parmetro
        // @arg El state del permissions store
        // @arg El estado de concesin del permiso de geolocalizacin
        setIsGeolocationPermissionGranted(state, isGeolocationPermissionGranted) {
            state.isGeolocationPermissionGranted = isGeolocationPermissionGranted;
        },

        // @vuese
        // Actualiza el valor del estado de concesin del permiso de notificaciones con el valor pasado como parmetro
        // @arg El state del permissions store
        // @arg El estado de concesin del permiso de notificaciones
        setIsPushNotificationPermissionGranted(state, isPushNotificationPermissionGranted) {
            state.isPushNotificationPermissionGranted = isPushNotificationPermissionGranted;
        }
    },
    actions: {
        // @vuese
        // Resetea el estado del permissions store
        // @arg El context del permissions store
        resetState(context) {
            return new Promise(function (resolve) {
                context.commit('resetState');
                resolve();
            });
        },

        // @vuese
        // Comprueba el estado del permiso de geolocalizacin y pide su concesin si todava no se ha requerido al usuario
        // @arg El context del permissions store
        checkForGeolocationPermission: function (context) {
            return new Promise(function (resolve, reject) {
                if (navigator.permissions == undefined) {
                    context.commit("setIsGeolocationPermissionGranted", false);
                    resolve(false);
                } else if (!context.getters.getIsGeolocationPermissionGranted) {
                    navigator.permissions.query({name: 'geolocation'}).then(function (result) {
                        if (result.state == 'granted') {
                            // Permission is already granted
                            context.commit("setIsGeolocationPermissionGranted", true);

                            context.dispatch('user/getLocation', null, {root: true}).catch((error) => {
                                reject(error);
                            });

                            resolve(true);
                        } else if (result.state == 'prompt') {
                            // Navigator will ask for permission
                            context.commit("setIsGeolocationPermissionGranted", false);
                            navigator.geolocation.getCurrentPosition(AuxMethods.emptyCallback);
                        } else if (result.state == 'denied') {
                            // User has denied permission
                            context.commit("setIsGeolocationPermissionGranted", false);
                            resolve(false);
                        }

                        result.onchange = function () {
                            // Whenever permission state changes

                            if (result.state == 'granted') {
                                // Permission is already granted
                                context.commit("setIsGeolocationPermissionGranted", true);

                                context.dispatch('user/getLocation', null, {root: true}).catch((error) => {
                                    reject(error);
                                });

                                resolve(true);
                            } else if (result.state == 'prompt') {
                                // Navigator will ask for permission
                                context.commit("setIsGeolocationPermissionGranted", false);
                                navigator.geolocation.getCurrentPosition(AuxMethods.emptyCallback);

                            } else if (result.state == 'denied') {
                                // User has denied permission
                                context.commit("setIsGeolocationPermissionGranted", false);
                                resolve(false);
                            }
                        }
                    });
                }
            });
        },

        // @vuese
        // Comprueba el estado del permiso de notificaciones y pide su concesin si todava no se ha requerido al usuario
        // @arg El context del permissions store
        checkForPushNotificationsPermission: function (context) {
            return new Promise(function (resolve, reject) {
                context.dispatch("database/askForMessagingPermission").then(function (isPermissionGranted) {
                    if (isPermissionGranted) {
                        context.commit("setIsPushNotificationPermissionGranted", true);
                        context.dispatch('user/generateNotificationToken', null, {root: true}).catch((error) => {
                            reject(error);
                        });
                        resolve(true);
                    } else {
                        context.commit("setIsPushNotificationPermissionGranted", false);
                        resolve(false);
                    }
                });
            });
        }
    }
}