<template>
    <v-app style="max-width: 100%; height: 100%;">
        <router-view v-if="dataIsReady" style="max-width: 100%; height: 100%;"/>
    </v-app>
</template>

<script>
    import {mapActions} from 'vuex';

    // Componente principal de la aplicacin
    // @group General
    export default {
        name: 'App',
        components: {},
        data() {
            return {
                dataIsReady: false
            }
        },
        methods: {
            ...mapActions('user', ['getRegistrationState', 'initState']),
            ...mapActions(['getLoggedUser']),
            // @vuese
            // Inicializa la aplicacin obteniendo los datos de la misma en funcin de si el usuario est o no loggeado y redirigiendo a esta a una parte de la aplicacin u otra en consecuencia
            initialize: function () {
                let self = this;
                self.getLoggedUser().then(currentUser => {
                    if (currentUser != null && currentUser != undefined) {
                        // user is logged in
                        let resultInitAppInfo = self.initState();

                        resultInitAppInfo
                            .then(() => {
                                self.dataIsReady = true;
                            })
                            .catch((error) => {
                                self.snackbarText = error;
                                self.snackbar = true;
                            });
                    } else {
                        self.$store.dispatch('resetAppState').then(() => {
                            self.$router.replace('home');
                        })
                            .catch((error) => {
                                self.snackbarText = error;
                                self.snackbar = true;
                            });

                        self.dataIsReady = true;
                        console.log("No user logged");
                    }
                });
            }
        },
        created() {
            this.initialize();
        }
    };
</script>

