import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

import Constants from "../Constants";
import database from "./database";
import permissions from "./permissions";
import users from "./users.js";
import user from "./user.js";


////////////////////////////////////////////////////////
//-----               APP RANKING                -----//
////////////////////////////////////////////////////////

// @vuese
// Mdulo del estado centralizado (Store) encargado de la gestin del ranking
export default {
    namespaced: true,
    modules: {
        database,
        permissions,
        users,
        user
    },
    state: {
        // @vuese
        // Referencia al ltimo usuario mostrado en el ranking y se utiliza para la paginacin del mismo
        lastUserShowed: -1,

        // @vuese
        // Almacena todos los usuarios del ranking obtenidos de la base de datos
        rankingUsers: [],

        // @vuese
        // Almacena los identificadores de los usuarios del ranking a mostrar
        rankingUsersIDs: [],
    },
    getters: {

        // @vuese
        // Utilizado para obtener los usuarios a mostrar con todos sus datos
        // @arg El state del ranking store
        // @arg Los getters del ranking store
        getRankingUsersToShow: (state, getters) => {
            let rus = [];

            let i = 0;
            state.rankingUsersIDs.forEach(uid => {
                let ru = {
                    ...getters['users/getUserByID'](uid)
                };

                ru["score"] = state.rankingUsers[i].score;
                ru["position"] = state.rankingUsers[i].position;

                rus.push(ru);
                i++;
            });

            return rus;
        },

        // @vuese
        // Utilizado para obtener el ltimo usuario del ranking mostrado
        // @arg El state del ranking store
        getLastUserShowed: (state) => {
            return state.lastUserShowed;
        },

        // @vuese
        // Utilizado para obtener los datos de un usuario segn su posicin en el ranking
        // @arg El state del ranking store
        // @arg El ndice del usuario del ranking a obtener
        getRankedUserByIndex: (state) => (index) => {
            return state.rankingUsers[index];
        },

        // @vuese
        // Utilizado para devolver todos los usuarios del ranking obtenidos de base de datos
        // @arg El state del ranking store
        getRankingUsers: (state) => {
            return state.rankingUsers;
        }
    },
    mutations: {
        // @vuese
        // Restablece todas las variables del state a sus valores por defecto
        // @arg El state del ranking store
        resetState(state) {
            state.lastUserShowed = -1;
            state.rankingUsers.splice(0, state.rankingUsers.length);
            state.rankingUsersIDs.splice(0, state.rankingUsersIDs.length);
        },

        // @vuese
        // Asigna los usuarios pasados como parmetro a la variable del estado rankingUsers
        // @arg El state del ranking store
        // @arg Array con los usuarios del ranking a aadir
        setRankingUsers(state, rankingUsers) {
            rankingUsers.forEach(r => {
                state.rankingUsers.push(r);
            });
        },

        // @vuese
        // Guarda la referencia al ltimo usuario del ranking mostrado
        // @arg El state del ranking store
        // @arg Referencia al ltimo usuario del ranking mostrado
        setLastUserShowed(state, lastUserShowed) {
            state.lastUserShowed = lastUserShowed;
        },

        // @vuese
        // Aade un nuevo ID de usuario a mostrar a rankingUsersIDs
        // @arg El state del ranking store
        // @arg ID del usuario a aadir a rankingUsersIDs
        addRankingUserID(state, rankingUserID) {
            if (-1 == state.rankingUsersIDs.findIndex(id => {
                return id == rankingUserID;
            })) {
                state.rankingUsersIDs.push(rankingUserID);
            }
        }
    },
    actions: {
        // @vuese
        // Inicia el state con los datos apropiados
        // @arg El context del ranking store
        initState(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('resetState')
                    .then(() => {
                        context.dispatch('getRankingUsers')
                            .then(() => {
                                console.log("tee")
                                resolve();
                            })
                            .catch((error) => {
                                reject(error)
                            });
                    })
                    .catch((error) => {
                        reject(error)
                    });
            });

        },

        // @vuese
        // Obtiene el top 100 de los usuarios del ranking
        // @arg El context del ranking store
        getRankingUsers(context) {
            return new Promise(function (resolve, reject) {
                context.dispatch('database/getTop100RankingUsers')
                    .then((top100Users) => {
                        context.commit('setRankingUsers', top100Users);
                        resolve();
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        // @vuese
        // Aade los siguientes usuarios a mostrar (tantos como el tamao de pgina establecido)
        // @arg El context del ranking store
        getPagedRankingUsers(context) {
            return new Promise(async function (resolve, reject) {
                let newUserShowed = context.getters.getLastUserShowed + 1;
                let pageEnding = newUserShowed + Constants.RANKING_PAGE_SIZE;
                if (newUserShowed == context.getters.getRankingUsers) {
                    resolve(false);
                }
                while (newUserShowed < pageEnding && newUserShowed < context.getters.getRankingUsers.length) {
                    let user = context.getters.getRankedUserByIndex(newUserShowed);

                    await context.dispatch('users/getUserByID', user.id)
                        .then(() => {
                            context.commit("addRankingUserID", user.id);
                        })
                        .catch((error) => {
                            reject(error);
                        });
                    newUserShowed++;
                }

                context.commit('setLastUserShowed', newUserShowed - 1);
                if (newUserShowed < context.getters.getRankingUsers.length) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            });
        },

        // @vuese
        // Determina si hay usuarios del ranking por mostrar
        // @arg El context del ranking store
        areThereMoreRankingUsersToShow(context) {
            return new Promise(async function (resolve) {
                let newUserShowed = context.getters.getLastUserShowed + 1;
                if (newUserShowed < context.getters.getRankingUsers.length) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            });
        },

        // @vuese
        // Resetea el estado del ranking store
        // @arg El context del ranking store
        resetState(context) {
            return new Promise(function (resolve) {
                context.commit('resetState');
                resolve();
            });
        }
    }
}