        // @vuese
        // Crea y establece en base de datos un evento
        // @arg El context del database store
        // @arg data {date, description, headerImg, location, involvedLanguages, name}
        createEvent: function (context, data) {
            return new Promise(function (resolve, reject) {
                let firestoreDB = firebase.firestore();
                let eventRef = firestoreDB.collection("Events").doc();

                data.location.coordinates = new firebase.firestore.GeoPoint(data.location.coordinates.lat, data.location.coordinates.lng);

                context.dispatch('uploadEventHeaderImg', {'img': data.headerImg, 'eventID': eventRef.id})
                    .then((url) => {
                        let eventData = {
                            name: data.name,
                            date: new Date(new Date(data.date).setHours(0, 0, 0)),
                            participants: [],
                            headerImg: url,
                            description: data.description,
                            involvedLanguages: data.involvedLanguages,
                            location: data.location
                        };
                        eventRef.set(
                            {
                                ...eventData
                            },
                            {
                                merge: true
                            }
                        )
                            .then(() => {
                                console.log("Event successfully created in firebase");
                                resolve({...eventData, id: eventRef.id});
                            })
                            .catch(function (error) {
                                console.error(error);
                                reject(genericDatabaseError);
                            });
                    })
                    .catch((error) => {
                        reject(error);
                    });
            });
        },

        // @vuese
        // Elimina de la base de datos un evento
        // @arg El context del database store
        // @arg ID del evento
        removeEvent: function (context, eventID) {
            return new Promise(function (resolve, reject) {
                    let firestoreDB = firebase.firestore();

                    firestoreDB.collection("Events").doc(eventID).delete()
                        .then(() => {
                            console.log("Event successfully removed from firebase");
                            resolve();
                        })
                        .catch(function (error) {
                            console.error(error);
                            reject(genericDatabaseError);
                        });

                }
            );
        },

        // @vuese
        // Actualiza en la base de datos la informacin de un evento
        // @arg El context del database store
        // @arg data {[date], [description], [headerImg], [location], [involvedLanguages], [name]}
        updateEvent: function (context, data) {
            return new Promise(async function (resolve, reject) {
                let firestoreDB = firebase.firestore();

                if (data.hasOwnProperty('location')) {
                    data.location.coordinates = new firebase.firestore.GeoPoint(data.location.coordinates.lat, data.location.coordinates.lng);
                }

                if (data.hasOwnProperty('headerImg')) {
                    await context.dispatch('uploadEventHeaderImg', {'img': data.headerImg, 'eventID': data.id})
                        .then((url) => {
                            data.headerImg = url;
                        })
                        .catch((error) => {
                            reject(error);
                        });
                }

                if (data.hasOwnProperty('date')) {
                    data.date = new Date(new Date(data.date).setHours(0, 0, 0));
                }

                firestoreDB.collection("Events").doc(data.id).update(
                    {
                        ...data
                    }
                )
                    .then(() => {
                        console.log("Event successfully updated in firebase");
                        let returnValue = {};

                        if (data.hasOwnProperty('headerImg')) {
                            returnValue['headerImg'] = data.headerImg;
                        }

                        if (data.hasOwnProperty('date')) {
                            returnValue['date'] = new Date(data.date)
                        }

                        resolve(returnValue);
                    })
                    .catch(function (error) {
                        console.error(error);
                        reject(genericDatabaseError);
                    });
            });
        },

        // @vuese
        // Obtiene de la base de datos la siguiente pgina de eventos
        // @arg El context del database store
        // @arg data{lastEventShowed, limit}
        getNextEventsPage: function (context, data) {
            return new Promise(function (resolve, reject) {

                let firestoreDB = firebase.firestore();
                let page;
                let newLastEventShowed;
                let lastEventShowed = data.lastEventShowed;
                let currentDate = new Date();

                page = firestoreDB.collection("Events");

                // --> LANGUAGES FILTERING
                if (data.filters.languageInvolved !== "all") {
                    page = page.where("involvedLanguages", "array-contains", data.filters.languageInvolved);
                }
                // --> END LANGUAGES FILTERING

                if (lastEventShowed == null) {
                    page = page.where("date", ">", currentDate).orderBy("date").startAt(lastEventShowed);
                } else {
                    page = page.where("date", ">", currentDate).orderBy("date").startAfter(lastEventShowed);
                }
                page.limit(data.limit).get()
                    .then(function (documentSnapshots) {

                        newLastEventShowed = documentSnapshots.docs[documentSnapshots.docs.length - 1];

                        let eventsRetrieved = documentSnapshots.docs.map(doc => {
                            const id = doc.id;
                            const dataDocs = doc.data();
                            return {id, ...dataDocs};
                        });

                        resolve({
                            newLastEventShowed: newLastEventShowed,
                            events: eventsRetrieved
                        });


                    }).catch((error) => {
                    console.log(error);
                    reject(genericDatabaseError);
                });
            });
        },

