export default {
    // @vuese
    // Comprueba si dos arrays contienen los mismos elementos
    // @arg Array 1
    // @arg Array 2
    arraysContainSameElements: function (arr1, arr2) {
        if (!Array.isArray(arr1) || !Array.isArray(arr2) || arr1.length !== arr2.length)
            return false;

        var arr1_aux = arr1.concat().sort();
        var arr2_aux = arr2.concat().sort();

        for (var i = 0; i < arr1_aux.length; i++) {

            if (arr1_aux[i] !== arr2_aux[i])
                return false;

        }
        return true;
    },

    // @vuese
    // Genera el ID de un chat entre dos usuarios
    // @arg ID de usuario 1
    // @arg ID de usuario 2
    generateChatID(id1, id2) {
        let chatID;

        if (id1 < id2) {
            chatID = id1 + id2;
        } else {
            chatID = id2 + id1;
        }

        return chatID;
    },

    // @vuese
    // No realiza ninguna accin
    emptyCallback: function () {
    },

}